@color: #f65c8f;
html {
    height: 100%;
}
body {
    width: 100%;
    height: 100%;
    background-color: @color;
    .body-main {
        height: 100%;
    }
}

.logo {
    padding: 0 65px;
    img {
        width: 100%;
    }
    height: 20%;
}

.account {
    padding: 0 40px;
    height: 48%;
    input {
        width: 100%;
        text-align: center;
        background-color: transparent;
        color: white; 
        border-left: medium none; 
        border-right: medium none; 
        border-top: medium none; 
        border-bottom: 1px solid #D0CFCF;
        outline:medium;
        padding: 12px 0;
        line-height: 18px;
        font-family: microsoft yahei;
        font-size: 18px;
    }
    input::-webkit-input-placeholder { /* WebKit browsers */
        color: white;
        font-size: 18px;
        opacity: 0.6;
        letter-spacing: 2px;
    }
    input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color: white;
        font-size: 18px;
        opacity: 0.6;
        letter-spacing: 2px;
    }
    input::-moz-placeholder { /* Mozilla Firefox 19+ */
        color: white;
        font-size: 18px;
        opacity: 0.6;
        letter-spacing: 2px;
    }
    input:-ms-input-placeholder { /* Internet Explorer 10+ */
        color: white;
        font-size: 18px;
        opacity: 0.6;
        letter-spacing: 2px;
    }
    .login-btn {
        width: 100%;
        margin-top: 17px;
        background-color: transparent;
        color: white;
        font-size: 17px;
        padding: 12px 0;
        letter-spacing: 5px;
    }

    .register-btn {
        width: 100%;
        margin-top: 17px;
        background-color: white;
        color: @color;
        font-size: 17px;
        padding: 12px 0;
        letter-spacing: 5px;
    }
}

.foot {
    padding: 0 40px;
    height: 32%;
    .am-vertical-align-middle {
        width: 100%;
    }
    .forgot-password {
        text-align: center;
        a {
            text-decoration: none;
            color: white;
            font-size: 14px;
        }
    }
    .third-login {
        position: relative;
        padding: 6px 0 7px 0;
        .line {
            border-top: 1px solid white;
        }
        p {
            position: absolute;
            font-size: 14px;
            color: white;
            margin: 0;
            line-height: 14px;
            top: 0;
            left: 50%;
            margin-left: -43px;
            width: 86px;
            background-color: @color;
            text-align: center;
        }
        margin-bottom: 1.6rem;
    }
    .login-link {
        .qq {
            float: left;
            width: 50%;
            text-align: center;
            img {
                width: 55px;

            }
        }
        .weibo {
            .qq
        }
    }
}
.warning {
    position: fixed;
    z-index: 999;
    width: 110px;
    height: 100px;
    background-color: #484848;
    border-radius: 7px;
    top: 40%;
    margin-top: -50px;
    left: 50%;
    margin-left: -50px;
    padding: 10px;
    text-align: center;
    display: none;
    p {
        color: #E6E6E6;
        font-size: 14px;
        margin: 0;
        text-align: center;      
    }
}
